import math from 'mathjs'
import { transform, identity } from '@/assets/line-town-matrix'
import { cube, pyramid, camera as cameraModel, slope } from '@/assets/line-town-models'

function element ({ model, x = 0, y = 0, z = 0, w = 1, h = 1, d = 1, ax = 0, ay = 0, az = 0, visible, color, opacity }) {
  const style = {}
  if (color !== undefined) style.color = color
  if (opacity !== undefined) style.opacity = opacity
  if (visible !== undefined) style.visible = visible

  const t = transform({ x, y, z, w, h, d, ax: ax / 180 * Math.PI, ay: ay / 180 * Math.PI, az: az / 180 * Math.PI })

  const element = {}
  if (model) Object.assign(element, model)
  element.style = Object.assign({}, element.style, style)
  element.transform = math.multiply(element.transform || identity(), t)
  return element
}

const roofColor = '#99d'

const camera = element({
  model: cameraModel,
  name: 'camera',
  x: -7,
  y: 10,
  z: -9,
  ax: 45,
  ay: 45 * 3,
  visible: false
})

const plate10 = element({ model: cube, name: 'plate10', x: 4.5, z: 4.5, w: 10, d: 10 })
const plate5 = element({ model: cube, name: 'plate5', x: 2, z: 2, w: 5, d: 5 })

const tent = {
  name: 'tent',
  children: [
    element({ model: slope, z: 0.5, w: 2 }),
    element({ model: slope, z: 1.5, w: 2, ay: 180 })
  ],
  transform: transform({ x: -0.5, z: -0.5 })
}

const house = {
  name: 'house',
  children: [
    element({ model: tent, y: 1, color: roofColor }),
    element({ model: cube, w: 2, d: 2, x: -0.5, z: 0.5, color: '#cba' })
  ]
}

const tree = {
  children: [
    element({ model: cube, h: 2, y: 0.5 })
  ],
  name: 'tree',
  image: {
    src: '/static/line-town/tree.png'
  }
}

const church = {
  name: 'church',
  children: [
    element({ model: cube, x: 0, y: 0, z: 0 }),
    element({ model: cube, x: 0, y: 1, z: 0 }),
    element({ model: cube, x: 0, y: 0, z: -1 }),
    element({ model: cube, x: 0, y: 0, z: -2 }),
    element({ model: pyramid, x: 0, y: 2, z: 0, color: roofColor })
  ]
}

const world = {
  name: 'world',
  children: [
    element({ model: cube, color: 'cyan', opacity: 0.3 }),
    element({ model: tent, x: 2, z: 7, color: 'orange' }),
    element({ model: tent, z: 4, ay: 90, color: 'green' }),
    element({ model: plate10, y: -1 }),
    element({ model: plate10, y: -1, x: -10 }),
    element({ model: plate10, y: -1, x: -10, z: -10 }),
    element({ model: plate10, y: -1, z: -10 }),
    element({ model: church, x: 8, z: 2, ay: 270 }),
    element({ model: house, x: 7, z: -4 }),
    element({ model: house, x: 3, z: -7, ay: 90 }),
    element({ model: plate5, x: -8, z: -7 }),
    element({ model: slope, x: -9, z: -4, w: 3, ay: 270 }),
    element({ model: slope, x: -5.5, z: -8.5, w: 2, d: 2 }),
    element({ model: cube, x: -5, z: -2, w: 3 }),
    element({ model: cube, y: 1, x: -7, z: -5 }),
    element({ model: cube, y: 1, x: -4, z: -7 }),
    element({ model: cube, y: 2, x: -4, z: -7 }),
    element({ model: pyramid, x: -7, y: 2, z: -5, color: roofColor }),
    element({ model: tree, x: -4, z: 2 }),
    element({ model: tree, x: -4, z: 3 }),
    element({ model: tree, x: -4, z: 4 }),
    element({ model: tree, x: -8, z: 5 }),
    camera
  ]
}

export { world, camera }



