<template>
  <section fluid>
    <h1>Line Town</h1>
    <p>The following demonstates are rather crude 3D renderer. Some faces get rendered in the wrong order, the effect is more apparent when spinning the scene. It is possible to write a more advanced polygon sorting method, but a perfect one is quite tricky. This code makes no use of any 3D libaries; they do a much better job!</p>
    <p>
      <button
        type="button"
        @click="spin">{{ spinning ?'Stop': 'Spin' }}</button>
    </p>
    <canvas
      width="500"
      height="500" />
  </section>
</template>
<script>
import { world, camera } from '@/assets/line-town-world'
import { rotate } from '@/assets/line-town-matrix'
import { initializeElement, loadImages, transformElement, cascadeStyles } from '@/assets/line-town-pre-process'
import { renderElement } from '@/assets/line-town-render'
import math from 'mathjs'

export default {
  name: 'LineTown',
  components: {},
  data () {
    return {
      spinning: false,
      world: null
    }
  },
  beforeDestroy () {
    clearInterval(this.interval)
  },
  mounted () {
    const canvas = document.getElementsByTagName('canvas')[0]
    camera.context = canvas.getContext('2d')
    camera.scale = 20
    camera.width = canvas.width
    camera.height = canvas.height
    camera.center = { x: camera.width / 2, y: camera.height / 2 }

    loadImages(world, () => {
      initializeElement(world)
      const transformedWorld = transformElement(world)
      cascadeStyles(transformedWorld)
      this.world = transformedWorld

      this.render()
      this.interval = setInterval(this.render, 25)
    })
  },
  methods: {
    render () {
      const degree = Math.PI * 2 / 360
      const rotation = rotate({y: 0.5 * degree})

      // TODO don't move this camera, move the one in this.world instead
      if (this.spinning) {
        camera.transform = math.multiply(camera.transform, rotation)
      }
      renderElement(camera, this.world)
    },
    spin () {
      this.spinning = !this.spinning
    }
  }
}

</script>

<style lang="scss" scoped>
#tree {
  position: absolute;
  top: -1000px;
}
</style>



