import math from 'mathjs'
import { identity } from '@/assets/line-town-matrix'

function projectElement (element, cameraTransform = identity()) {
  const result = Object.assign({}, element)

  if (element.children) {
    result.children = []
    element.children.forEach(child => {
      result.children.push(projectElement(child, cameraTransform))
    })
  }

  if (element.points) {
    result.normals = math.multiply(element.normals, cameraTransform)
    result.points = math.multiply(element.points, cameraTransform)
  }

  result.bounds = math.multiply(element.bounds, cameraTransform)

  return result
}

function generatePolys (element) {
  if (element.style.visible === false) return

  if (element.children) {
    element.children.forEach(child => generatePolys(child))
  }

  if (element.faces) {
    element.polys = []
    element.faces.forEach(face => {
      const points = []
      face.forEach(point => points.push(element.points[point]))
      element.polys.push({ points })
    })
  }
}

function extractPolys (element, result = []) {
  if (element.image) {
    result.push({
      image: element.image,
      midPoint: element.bounds[1],
      style: element.style
    })
  } else {
    if (element.children) {
      element.children.forEach(child => extractPolys(child, result))
    }

    if (element.polys) {
      for (let i = 0; i < element.polys.length; i++) {
        result.push({
          points: element.polys[i].points,
          normal: element.normals[i],
          style: element.style
        })
      }
    }
  }

  return result
}

function sortPolys (polys) {
  polys.sort(sortByLowestZ)

  function sortByLowestZ (a, b) {
    let az = getLowestZ(a)
    let bz = getLowestZ(b)
    if (az > bz) {
      return 1
    } else if (az < bz) {
      return -1
    }
    return 0
  }

  function getLowestZ (poly) {
    if (!poly.points) {
      if (poly.midPoint) return poly.midPoint[2]
      return 0
    }
    let z = poly.points[0][2]
    for (let i = 1; i < poly.points.length; i++) {
      z = Math.min(poly.points[i][2], z)
    }
    return z
  }
}

export { projectElement, generatePolys, sortPolys, extractPolys }



