const cube = {
  points: [
    [-0.5, -0.5, -0.5],
    [+0.5, -0.5, -0.5],
    [+0.5, -0.5, +0.5],
    [-0.5, -0.5, +0.5],

    [-0.5, +0.5, -0.5],
    [+0.5, +0.5, -0.5],
    [+0.5, +0.5, +0.5],
    [-0.5, +0.5, +0.5]
  ],
  faces: [
    [4, 5, 1, 0],
    [2, 1, 5, 6],
    [7, 3, 2, 6],
    [7, 4, 0, 3],
    [0, 1, 2, 3],
    [7, 6, 5, 4]
  ]
}
const slope = {
  points: [
    [-0.5, -0.5, -0.5],
    [-0.5, -0.5, +0.5],
    [+0.5, -0.5, +0.5],
    [+0.5, -0.5, -0.5],

    [-0.5, +0.5, -0.5],
    [-0.5, +0.5, +0.5],
    [+0.5, +0.5, +0.5],
    [+0.5, +0.5, -0.5]
  ],
  faces: [
    [1, 5, 0],
    [2, 6, 5, 1],
    [3, 6, 2],
    [0, 3, 2, 1],
    [3, 0, 5, 6]
  ]
}

const pyramid = {
  points: [
    [-0.5, -0.5, -0.5],
    [+0.5, -0.5, -0.5],
    [+0.5, -0.5, +0.5],
    [-0.5, -0.5, +0.5],
    [0, +0.5, 0]
  ],
  faces: [
    [0, 1, 2, 3],
    [0, 4, 1],
    [2, 1, 4],
    [3, 2, 4],
    [0, 3, 4]
  ]
}

const camera = {
  points: [
    [-0.5, -0.5, -0.5],
    [+0.5, -0.5, -0.5],
    [+0.25, -0.25, +0.5],
    [-0.25, -0.25, +0.5],

    [-0.5, +0.5, -0.5],
    [+0.5, +0.5, -0.5],
    [+0.25, +0.25, +0.5],
    [-0.25, +0.25, +0.5]
  ],
  faces: [
    [4, 5, 1, 0],
    [2, 1, 5, 6],
    [7, 3, 2, 6],
    [7, 4, 0, 3],
    [0, 1, 2, 3],
    [7, 6, 5, 4]
  ]
}

export { cube, pyramid, camera, slope }



