import math from 'mathjs'
import color from 'color'
import { projectElement, generatePolys, sortPolys, extractPolys } from '@/assets/line-town-process'

function clearCanvas (camera) {
  const ctx = camera.context
  ctx.fillStyle = '#dfc'
  ctx.fillRect(0, 0, camera.width, camera.height)
}

function renderElement (camera, element) {
  const cameraTransfrom = math.inv(camera.transform)
  const projectedElement = projectElement(element, cameraTransfrom)
  generatePolys(projectedElement)
  const polys = extractPolys(projectedElement)
  sortPolys(polys)

  clearCanvas(camera)
  renderPolys(camera, polys)
}

function renderPolys (camera, polys) {
  polys.forEach(poly => renderPoly(camera, poly))
}

function renderPoly (camera, poly) {
  const ctx = camera.context
  const s = camera.scale
  const o = camera.center
  if (poly.image) {
    const w = poly.image.object.width
    const h = poly.image.object.height
    ctx.drawImage(
      poly.image.object,
      o.x + poly.midPoint[0] * s - w / 2,
      o.y - poly.midPoint[1] * s - h / 2,
      w,
      h)
  } else if (poly.points) {
    let opacity = poly.style.opacity
    if (opacity === undefined) opacity = 1
    ctx.lineWidth = opacity
    ctx.fillStyle = getPolyColor(poly)
    ctx.strokeStyle = '#000'
    ctx.beginPath()
    if (isPolyFacingCamera(poly) || opacity !== 1) {
      ctx.moveTo(poly.points[0][0] * s + o.x, -poly.points[0][1] * s + o.y)
      for (let i = 1; i < poly.points.length; i++) {
        ctx.lineTo(poly.points[i][0] * s + o.x, -poly.points[i][1] * s + o.y)
      }
      ctx.closePath()
      ctx.stroke()
      ctx.fill()
    }
  }

  function getPolyColor (poly) {
    var base = color(poly.style.color || '#eee')
    let opacity = poly.style.opacity
    if (opacity === undefined) opacity = 1

    if (opacity !== 1) base = base.alpha(opacity)

    if (poly.normal[1] > 0) {
      if (poly.normal[0] > 0) {
        return base.string()
      } else {
        return base.darken(0.1).string()
      }
    } else {
      return base.lighten(0.1).string()
    }
  }

  function isPolyFacingCamera (poly) {
    return poly.normal[2] <= 0
  }
}

export { renderElement }



