import math from 'mathjs'
import { identity } from '@/assets/line-town-matrix'

function transformElement (element, transform = identity()) {
  const result = Object.assign({}, element)

  const combinedTransform = math.multiply(
    element.transform || identity(),
    transform)

  if (element.children) {
    result.children = []
    element.children.forEach(child => {
      result.children.push(transformElement(child, combinedTransform))
    })
  }

  if (element.points) {
    result.normals = math.multiply(element.normals, combinedTransform)
    result.points = math.multiply(element.points, combinedTransform)
  }

  result.bounds = math.multiply(element.bounds, combinedTransform)

  delete result.transform

  return result
}

function cascadeStyles (element, style = null) {
  element.style = Object.assign({}, style, element.style)

  if (element.children) {
    element.children.forEach(child => {
      cascadeStyles(child, element.style)
    })
  }
}

function generateForthPoints (element) {
  if (!element.points) return
  element.points.forEach(point => {
    if (point.length < 4) {
      point.push(1)
    }
  })
}

function generateFaceNormals (element) {
  if (element.normals || !element.faces) return
  element.normals = []
  element.faces.forEach(face => {
    const vertex1 = element.points[face[0]]
    const vertex2 = element.points[face[1]]
    const vertex3 = element.points[face[2]]
    const line1 = math.subtract(vertex2, vertex1)
    const line2 = math.subtract(vertex3, vertex2)
    line1.pop()
    line2.pop()
    const normal = math.cross(line2, line1)
    normal.push(0)
    element.normals.push(normal)
  })
}

function initializeElement (element) {
  if (element.children) {
    element.children.forEach(child => initializeElement(child))
  }
  generateForthPoints(element)
  generateFaceNormals(element)
  generateBounds(element)
}

function generateBounds (element) {
  let minPoint = [Infinity, Infinity, Infinity, 1]
  let maxPoint = [-Infinity, -Infinity, -Infinity, 1]
  let compared = false

  if (element.children) {
    element.children.forEach(child => {
      minPoint[0] = Math.min(minPoint[0], child.bounds[0][0])
      minPoint[1] = Math.min(minPoint[1], child.bounds[0][1])
      minPoint[2] = Math.min(minPoint[2], child.bounds[0][2])
      maxPoint[0] = Math.max(maxPoint[0], child.bounds[2][0])
      maxPoint[1] = Math.max(maxPoint[1], child.bounds[2][1])
      maxPoint[2] = Math.max(maxPoint[2], child.bounds[2][2])
      compared = true
    })
  }

  if (element.points) {
    element.points.forEach(point => {
      minPoint[0] = Math.min(minPoint[0], point[0])
      minPoint[1] = Math.min(minPoint[1], point[1])
      minPoint[2] = Math.min(minPoint[2], point[2])
      maxPoint[0] = Math.max(maxPoint[0], point[0])
      maxPoint[1] = Math.max(maxPoint[1], point[1])
      maxPoint[2] = Math.max(maxPoint[2], point[2])
      compared = true
    })
  }

  if (!compared) {
    minPoint = [0, 0, 0, 1]
    maxPoint = [0, 0, 0, 1]
  }

  const midPoint = [
    (minPoint[0] + maxPoint[0]) / 2,
    (minPoint[1] + maxPoint[1]) / 2,
    (minPoint[2] + maxPoint[2]) / 2,
    1
  ]

  element.bounds = [minPoint, midPoint, maxPoint]
}

function loadImages (element, done, images = {}) {
  if (element.children) {
    let callbacks = element.children.length
    const callback = () => {
      if (--callbacks !== 0) return
      loadImage(element, images, done)
    }
    element.children.forEach(child => {
      loadImages(child, callback, images)
    })
  } else {
    loadImage(element, images, done)
  }

  function loadImage (element, images, done) {
    if (element.image) {
      if (images[element.image.src]) {
        done()
      } else {
        const img = new Image()
        img.onload = done
        img.src = element.image.src
        element.image.object = img
      }
    } else {
      done()
    }
  }
}

export { initializeElement, loadImages, transformElement, cascadeStyles }



